import * as u_dt from '../../../../update/Update_DirectionType'
import * as u_sn from '../update/MRU_SectionName'
import * as uc_sn from '../../cell/update/MCU_SectionName'
import * as dt from './MR_DeleteType'
import * as tct from '../../command/CommandType'

export class MR_Delete {
  Run = (data, handleUndo) => {
    // undo
    undoFuncs = undefined
    if (handleUndo) {
      var undoFuncs = []
      handleUndo(() => {
        undoFuncs.forEach(x => x())
      });
    }

    var deleteType = data.DeleteType
    var q = this.Host
    var table = q.Table

    var supItem = q.SupItem;
    var list = q.CurrentList;

    if (deleteType === dt.removedata) {
      if (q.IsCreatedByUser) {
        q.MassGroupItem.GetItem(q.FullValue).Delete(
          // undo
          (undo) => {
            if (handleUndo) {
              undoFuncs.push(undo)
            }
          });
      } else {
        q.MassGroupItem.AddNotIncludeValue(q.FullValue,
          // undo
          (undo) => {
            if (handleUndo) {
              undoFuncs.push(undo)
            }
          });
      }
    }

    if (supItem && list.length === 1) {
      data.IsContinue = true
    } else {
      data.IsContinue = false

      var index = q.Index

      // Undo
      if (handleUndo) {
        undoFuncs.push(() => {
          list.splice(index, 0, q)
        });
      }
      // Do
      list.splice(index, 1);

      if (supItem) {
        // Undo
        if (handleUndo) {
          undoFuncs.push(() => {
            supItem.Update.Run(
              [
                { Section: u_sn.celldata, Data: { Sections: uc_sn.subcells } },
                u_sn.unitlist
              ],
              u_dt.both
            )
          })

          // Thc hin undo
          supItem.UnitData.SetUndo((undo) => {
              undoFuncs.push(undo);
            });
        }
        // Do
        supItem.Update.Run(
          [
            { Section: u_sn.celldata, Data: { Sections: uc_sn.subcells } },
            u_sn.unitlist, u_sn.unit],
          u_dt.upper
        )
      }
    }
  }
}