import { SelectVM } from "../../../../../../select/SelectVM";
import * as textUtil from "../../../../../../text/TextVMUtil";
import * as genUtil from '../../../../../../../util/generalUtil';
import * as rs from './MR_Unit_RenderState'
import * as rUtil from '../base/MassRowUtil'
import * as u_sn from '../update/MRU_SectionName'
import * as u_dt from '../../../../update/Update_DirectionType'
import * as uc_sn from '../../cell/update/MCU_SectionName'
import * as tct from '../../command/CommandType'

// Instance


// Property
export const GetUpdate_DirectionType = (q) => {
  return q.Dict.SubItems.length === 0 ? u_dt.upper
    : u_dt.none
}

export const GetUnit_SelectVM = (q) => {
  var qI = new SelectVM();
  qI.Placeholder = '...'

  const setObj = () => {
    var data = q.Units
    qI.Items = data;

    // Ch lm vic ny khi khi to SelectVM
    if (!q.Is_Unit_Select_Created) {
      var host = q.Dict;
      q.Is_Unit_Select_Created = true;

      if (q.Update_DirectionType === u_dt.upper) {
        q.SetUnit_FromHost(rUtil.Get_DefaultUnit(host));
      }
    }
  }

  if (q.Units) {
    setObj()
  }

  // Khi cp nhp Units, t ng cp nhp List  trn
  genUtil.Add_2(
    q.OnSet_Unit_Func,
    setObj, false)

  qI.OnSelectedItemChanged = (v) => {
    if (!q.IsUndo) {
      q.IsUndo = true;
    } else {
      var table = q.Table
      SetUndo(q, (undo) => {
      table.CommandDict.Add(
        {
          Key: tct.changeunitrow,
          Undo: undo
        })
      });
    }
    q.CurrentUnit = v;
  }
  qI.OnSetOpen = (v) => {
    if (!v) {
      q.RenderState = rs.text
    }
  }

  return qI;
}

export const GetUnit_RenderVM = (q) => {
  var qI = textUtil.get(q.CurrentUnit)
  return qI;
}

// Units
export const ReHandle_Units = (q) => {
  q.IsGet_Units = false
  Handle_Units(q);
}

export const Handle_Units = (q) => {
  if (q.IsGet_Units) return undefined;
  q.IsGet_Units = true;

  var host = q.Dict;

  const setObj = () => {
    var list = host.Cells.map(x => x.TotalValueData);
    var units

    list.forEach((x, i) => {
      if (i === 0) {
        units = x.map(y => y.Unit);
      } else {
        var deleteIndexs = []
        units.forEach((y, j) => {
          var item = genUtil.SingleFilter(x, (a) => y === a.Unit);
          if (!item) {
            deleteIndexs.push(j);
          }
        })

        deleteIndexs.forEach((y, j) => {
          units.splice(y - j, 1);
        })
      }
    })

    q.Units = units;
  }

  if (host.Is_HandleTotalData_Cells) {
    setObj()
  } else {
    genUtil.Add_2(
      host.On_HandleTotalData_Cells_Func,
      setObj
    )
  }
}

export const Set_CurrentUnit = (q, v) => {
  var dirType = q.Update_DirectionType
  var qI = q.Dict;

  // Update Data hin ti
  qI.Update.Run({
    Section: u_sn.celldata,
    Data: { Sections: uc_sn.unit }
  }, u_dt.none)

  if (dirType !== u_dt.none) {
    qI.Update.Run(u_sn.unit, dirType, false)
  }

  q.Update_DirectionType = u_dt.both
}

// Method
export const SetUndo = (q, handleUndo) => {
  var oldUnit = q.CurrentUnit;

  var undo
  if (oldUnit) {
    undo = () => {
      q.SetUnit_FromHost(oldUnit);
    }
  } else {
    // Lu cc  c Unit bn trong
    var dict = []
    var row = q.Dict;
    const getRows = (r) => {
      var unit = r.UnitData.CurrentUnit;
      if (r !== row && r.UnitData.CurrentUnit) {
        dict.push({
          Row: r,
          Unit: unit,
          DirectionType: u_dt.lower
        })
      } else {
        dict.push({
          Row: r,
          Unit: undefined,
          DirectionType: r === row ? u_dt.upper : u_dt.none
        })
        r.SubItems.forEach(x => getRows(x));
      }
    }

    getRows(row);

    undo = () => {
      dict.forEach(x => {
        x.Row.Update.Run({
          Section: u_sn.unit,
          Data: { Unit: x.Unit }
        }, x.DirectionType)
      })
    }
  }

  handleUndo(undo);
}